.galary {
  background-color: #eee;
  text-align: center;
}
.galary .galary-container {
  padding-bottom: 20px;
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
  align-items: normal;
  justify-content: center;
}
.galary .galary-container .img {
  width: 100%;
  max-width: 220px;
  aspect-ratio: 1;
  height: 220px;
  position: relative;
}
.galary .galary-container .img img {
  width: 100%;
  height: 100%;
}
.galary .galary-container .img h2 {
  background-color: rgba(191, 10, 48, 0.8);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  color: #fff;
  display: flex;
  gap: 0;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  transition: 0.3s;
  user-select: none;
}
.galary .galary-container .img:hover h2 {
  opacity: 1;
  transition: 0.3s;
}